
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.ready_area{
  text-align: left;
  margin-top:20px;
}
input{
  height: 25px;
  width: 200px;
  padding:0px 5px;
}
select{
   height: 30px;
   width: 200px;
}
.required>span{
   color: red;
}
.formArea{
   width: 100%;
}
.columnArea{
   float: left;
   min-height: 60px;
}
.columnArea>span{
   position: relative;
   float: left;
   line-height: 30px;
   width: 25%;
   text-align: right;
}
.columnArea>div{
   position: relative;
   float: left;
   width: 70%;
}
.columnArea>div>input{
   width: 100%;
}
.fd-w25{
   width: 25%;
}
.float_left{
   float: left;
}
.errorMsg{
   position: absolute;
   left:0px;
   top:35px;
   color: red;
}
